import React from "react";
import {
    TextField,
    Select,
    MenuItem,
    Checkbox,
    Radio,
    FormControlLabel,
    RadioGroup,
} from "@mui/material";
import { DatePicker } from "@mui/x-date-pickers/DatePicker";
import { Logger } from "../../../Logger/Logger";
import { AdapterDayjs } from "@mui/x-date-pickers/AdapterDayjs";
import { LocalizationProvider } from "@mui/x-date-pickers/LocalizationProvider";
import KeyboardArrowDownRoundedIcon from "@mui/icons-material/KeyboardArrowDownRounded";
import DateRangeOutlinedIcon from "@mui/icons-material/DateRangeOutlined";

const ITEM_HEIGHT = 42;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4,
        },
    },
    anchorOrigin: {
        vertical: "center",
        horizontal: "center",
    },
    transformOrigin: {
        vertical: "center",
        horizontal: "center",
    },
};

const PreviewQuestions = ({ question }) => {
    let questionLabel = question.questionTitle;
    let questionComponent =
        question.inputType === "singleTextbox" ? (
            <TextField
                placeholder={"Enter text here"}
                className="input-field"
                InputProps={{
                    readOnly: true,
                }}
                // disabledgit
            />
        ) : question.inputType === "textarea" ? (
            <TextField
                multiline
                InputProps={{
                    readOnly: true,
                }}
                className="input-textarea"
                placeholder="Enter text here"
                variant="outlined"
            />
        ) : //   <TextField

        //     multiline={5}
        //     id="outlined-basic"
        //     variant="outlined"
        //     placeholder={"Enter text here"}
        //   />
        question.inputType === "dropdown" ? (
            <div className="form-group">
                <div className="select-field">
                    <Select
                        IconComponent={(props) => (
                            <KeyboardArrowDownRoundedIcon {...props} />
                        )}
                        value={question.questionTitle}
                        MenuProps={MenuProps}
                        renderValue={() => (
                            <div className="select-placeholder">
                                Choose dropdown value
                            </div>
                        )}
                    >
                        {/* <MenuItem value={question.questionTitle} disabled>
                            Choose dropdown value
                        </MenuItem> */}
                        {question.options.map((option) => (
                            <MenuItem key={option} value={option} disabled>
                                {option}
                            </MenuItem>
                        ))}
                    </Select>
                </div>
            </div>
        ) : question.inputType === "radioGroup" ? (
            <div className="radio-btn-field radio-btn-half-blk">
                <RadioGroup
                    aria-labelledby="demo-radio-buttons-group-label"
                    defaultValue="Active"
                    name="radio-buttons-group"
                    className="radio-btn radio-btn-vertical"
                >
                    {question.options.map((option) => (
                        <FormControlLabel
                            key={option}
                            value={option}
                            control={<Radio disabled />}
                            label={option}
                        />
                    ))}
                </RadioGroup>
            </div>
        ) : question.inputType === "checkbox" ? (
            <div className="checkbox-with-labelblk checkbox-btn-half-blk">
                {question.options.map((option) => (
                    <FormControlLabel
                        key={option}
                        className="checkbox-with-label checkbox-flex"
                        control={<Checkbox disabled />}
                        label={option}
                    />
                ))}
            </div>
        ) : question.inputType === "date" ? (
            <LocalizationProvider dateAdapter={AdapterDayjs}>
                <DatePicker
                    disabled
                    value={null}
                    className="datepicker-blk"
                    components={{
                        OpenPickerIcon: DateRangeOutlinedIcon,
                    }}
                    onChange={() => {}}
                    renderInput={(params) => (
                        <TextField {...params} helperText=" " />
                    )}
                />
            </LocalizationProvider>
        ) : (
            ""
        );
    return (
        <div className="preview-que-blk">
            <div className="form-group">
                <label htmlFor="questionTitle">
                    <div className="preview-sect-txt">
                        {questionLabel}
                        {question?.isRequired && (
                            <span className="mandatory"> *</span>
                        )}
                    </div>
                </label>
                <div className="que-half-sect">{questionComponent}</div>
            </div>
        </div>
    );
};

export default PreviewQuestions;
